import React, { useEffect, useState } from "react";
import { Form, Input, Button } from "antd";
import axios from "axios";

const BasicSettings = () => {
  const [form] = Form.useForm();
  const [config, setConfig] = useState([]);
  useEffect(() => {
    axios.get("http://localhost:4000/v1.0/settings/basicconfig").then((res) => {
      if (res.status === 200) {
        setConfig(res.data.basicConfig);
      }
    });
  });

  return (
    <Form form={form} layout="vertical">
      <Form.Item label="Sq.ft Charge">
        <Input value={config.sqft_charge} style={{ width: 350 }} />
      </Form.Item>
      <Form.Item label="Labour Charge">
        <Input value={config.labour_charge} style={{ width: 350 }} />
      </Form.Item>
      <Form.Item>
        <Button type="primary">Update Information</Button>
      </Form.Item>
    </Form>
  );
};

export default BasicSettings;
