import React, { Component } from "react";
import { Table } from "antd";
import axios from "axios";

const { Column } = Table;

class StaffDetails extends Component {
  constructor() {
    super();
    this.state = {
      staffDetails: [],
      isLoading: true,
    };
  }
  componentDidMount() {
    axios
      .get("http://localhost:4000/v1.0/staff-management/staffdetails")
      .then((res) => {
        if (res.status === 200) {
          this.setState({
            staffDetails: res.data.staffDetails,
            isLoading: false,
          });
        }
      });
  }
  render() {
    const { staffDetails, isLoading } = this.state;
    return (
      <Table loading={isLoading} dataSource={staffDetails}>
        <Column title="Full Name" dataIndex="staff_name" key="staff_phone_no" />
        <Column
          title="Mobile No"
          dataIndex="staff_phone_no"
          key="staff_phone_no"
        />
      </Table>
    );
  }
}

export default StaffDetails;
