import React, { useRef, useState } from "react";
import { Form, Input, Button, DatePicker, InputNumber } from "antd";
const formItemLayout = {
  labelCol: {
    xs: {
      span: 24,
    },
    sm: {
      span: 8,
    },
  },
  wrapperCol: {
    xs: {
      span: 24,
    },
    sm: {
      span: 16,
    },
  },
};
const tailFormItemLayout = {
  wrapperCol: {
    xs: {
      span: 24,
      offset: 0,
    },
    sm: {
      span: 16,
      offset: 8,
    },
  },
};

const WorkRegistration = () => {
  const [form] = Form.useForm();
  const [estimateDisabled, setEstimateDisabled] = useState(true);
  const estimateInput = useRef(null);

  const claculateEstimate = (evt) => {
    let area = evt.target.value;
    console.log(area);
    let estimate = area * 6;
    estimateInput.current.value = estimate;
  };

  const onFinish = (values) => {
    console.log("Received values of form: ", values);
  };

  const handleEstimateClick = () => {
    setEstimateDisabled(false);
  };

  return (
    <Form
      {...formItemLayout}
      form={form}
      name="workregistration"
      onFinish={onFinish}
      scrollToFirstError
    >
      <Form.Item
        name="workName"
        label="Work Name"
        rules={[
          {
            required: true,
            message: "Please input Work Name!",
          },
        ]}
      >
        <Input style={{ width: 350 }} />
      </Form.Item>
      <Form.Item
        name="date"
        label="Date"
        rules={[
          {
            type: "date",
            message: "The input is not valid date!",
          },
          {
            required: true,
            message: "Please input date!",
          },
        ]}
      >
        <DatePicker format="DD-MM-YYYY" style={{ width: 350 }} />
      </Form.Item>
      <Form.Item
        name="place"
        label="Place"
        rules={[
          {
            required: true,
            message: "Please input place!",
          },
        ]}
      >
        <Input style={{ width: 350 }} />
      </Form.Item>
      <Form.Item
        name="area"
        label="Area (sq.ft)"
        rules={[
          {
            required: true,
            message: "Please input area!",
          },
        ]}
      >
        <InputNumber onBlur={claculateEstimate} style={{ width: 350 }} />
      </Form.Item>
      <Form.Item name="estimate" label="Estimate Amount">
        <InputNumber
          ref={estimateInput}
          disabled={estimateDisabled}
          style={{ width: 250 }}
        />
        <Button
          onClick={handleEstimateClick}
          style={{ width: 90 }}
          type="default"
        >
          Edit
        </Button>
      </Form.Item>
      <Form.Item {...tailFormItemLayout}>
        <Button type="primary" htmlType="submit">
          Create
        </Button>
      </Form.Item>
    </Form>
  );
};

export default WorkRegistration;
