import React, { useState, useEffect } from "react";
import { Routes, Route, Link, useNavigate } from "react-router-dom";
import { Layout, Menu, Avatar, Tag, Dropdown, Modal } from "antd";
import { BookOutlined, SettingOutlined, TeamOutlined } from "@ant-design/icons";
// import GuardedRoute from "../../utils/GuardedRoute";

import WorkManagement from "../workManagement";
import StaffManagement from "../staffManagement";
import Settings from "../settings";

// import "./index.scss";

// const { Header, Content, Footer, Sider } = Layout;
// const { SubMenu } = Menu;

const Navigation = () => {
  const { Content, Sider, Footer } = Layout;
  return (
    <Layout style={{ minHeight: "100vh" }}>
      <Sider>
        <Menu theme="dark" mode="inline" defaultSelectedKeys={["1"]}>
          <Menu.Item key="1" icon={<BookOutlined />}>
            <Link to="/">Work Management</Link>
          </Menu.Item>
          <Menu.Item key="2" icon={<TeamOutlined />}>
            <Link to="staffmanagement">Staff Management</Link>
          </Menu.Item>
          <Menu.Item key="3" icon={<SettingOutlined />}>
            <Link to="settings">Settings</Link>
          </Menu.Item>
        </Menu>
      </Sider>
      <Layout className="site-layout">
        <Content
          className="site-layout-background"
          style={{
            margin: "24px 16px",
            padding: 24,
            height: "100%",
          }}
        >
          <Routes>
            <Route path="/" element={<WorkManagement />} />
            <Route path="staffmanagement" element={<StaffManagement />} />
            <Route path="settings" element={<Settings />} />
          </Routes>
        </Content>
        <Footer style={{ textAlign: "center" }}>nithin.antony design</Footer>
      </Layout>
    </Layout>
  );
};

export default Navigation;
